<template>
  <div class="register">
    <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
      <el-form-item label="帐号" prop="code">
        <el-input type="text" v-model="ruleForm.code" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="密码" prop="pass">
        <el-input type="password" v-model="ruleForm.pass" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="确认密码" prop="checkPass">
        <el-input type="password" v-model="ruleForm.checkPass" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')">提交</el-button>
        <el-button @click="resetForm('ruleForm')">重置</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  export default {
    data() {
      var code = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入帐号'));
        } else if(value.length < 6){
          callback(new Error('帐号最少6位'));
        }else{
          callback();
        }
      };
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入密码'));
        } else {
          if (this.ruleForm.checkPass !== '') {
            this.$refs.ruleForm.validateField('checkPass');
          }
          callback();
        }
      };
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请再次输入密码'));
        } else if (value !== this.ruleForm.pass) {
          callback(new Error('两次输入密码不一致!'));
        } else {
          callback();
        }
      };
      return {
        ruleForm: {
          code:'',
          pass: '',
          checkPass: ''
        },
        rules: {
          code: [
            { validator: code, trigger: 'blur' }
          ],
          pass: [
            { validator: validatePass, trigger: 'blur' }
          ],
          checkPass: [
            { validator: validatePass2, trigger: 'blur' }
          ]
        },
        users:JSON.parse(window.localStorage.getItem('users'))||[]
      };
    },
    methods: {
      submitForm(formName) {
        var index = this.users.findIndex((el)=>{
         return el.code == this.ruleForm.code
        })
        if(index != -1){
          alert("帐号已经被注册")
        }else{
          this.users.push(this.ruleForm)
          window.localStorage.setItem('users',JSON.stringify(this.users))
          this.$refs[formName].validate((valid) => {
            if (valid) {
              alert('注册成功')
              this.$router.push('./')
            } else {
              console.log('error submit!!');
              return false;
            }
          });
        }
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    }
  }
</script>

<style scoped>
.register{
  width: 30%;
  margin: 0 auto;
}
</style>