<template>
  <div class="login">
    <el-form status-icon  label-width="100px" class="demo-ruleForm">
      <el-form-item label="帐号" prop="code">
        <el-input type="text" v-model="code" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="密码" prop="pass">
        <el-input type="password" v-model="pass" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="toHome">登录</el-button>
        <el-button @click="toRegister">注册</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>

export default {
  data() {
    return {
      code:'',
      pass:'',
      users:JSON.parse(window.localStorage.getItem('users'))||[]
    }
  },
  methods: {
    toHome(){
      var index = this.users.findIndex((element)=>{
        return element.code == this.code
      })
      if(index !== -1){
        if(this.users[index].pass == this.pass){
          this.$router.push('./Home')
        }else{
          alert('密码错误')
        }
      }else{
        alert('帐号不存在')
      }
    },
    toRegister(){
      this.$router.push('./Register')
    }
  },
}
</script>

<style scoped>
.login{
  width: 30%;
  margin: 0 auto;
}
</style>